.ymt-processbar {
  position: fixed;
  top: 4.4rem;
  left: 0;
  width: 0;
  height: 0.3rem;
  background: #FF7800;
  z-index: 999;
}
@keyframes loadingPart {
  0% {
    width: 0;
  }
  40% {
    width: 10%;
  }
  100% {
    width: 30%;
  }
}
@keyframes loadingAll {
  0% {
    width: 30%;
  }
  100% {
    width: 100%;
  }
}
.ymt-processbar-part {
  width: 30%;
  animation: loadingPart linear 1.5s;
  animation-fill-mode: forwards;
}
.ymt-processbar-all {
  width: 0;
  animation: loadingAll ease-in 1s;
}
/*基础样式*/
.ymt-btn {
  display: inline-block;
  /*width: 35rem;*/
  width: 100%;
  height: 4.4rem;
  line-height: 4.4rem;
  padding: auto 1.2rem;
  text-align: center;
  border-radius: 0.5rem;
  border-style: none;
  font-size: 1.7rem;
