.checkbox-wrapper {
  position: relative;
  display: inline-block;
}
.checkbox-wrapper input {
  display: none;
}
.ymt-checkbox {
  position: relative;
  width: 2.1rem;
  height: 2.1rem;
  border: 1px solid #ccc;
  border-radius: 0.25rem;
  display: flex;
  align-items: center;
  display: -webkit-flex;
  -webkit-align-items: center;
  text-align: center;
}
.ymt-checkbox.checked {
  color: #fff;
  transition: all 0.3s;
  background-color: #ff9600;
  border: 1px solid #ff9600;
}
.ymt-checkbox.un-checked {
  background: #fff;
}
.ymt-checkbox.un-checked .iconfont {
  visibility: hidden;
}
.ymt-checkbox .iconfont {
  font-size: 1.7rem;
  color: #FFFFFF;
}
