  padding: 2.2rem 0 0 0;
  font-size: 1.5rem;
  color: #2d2d2d;
}
.ymt-cars .content .item .right .right-two .sub {
  padding: 1.2rem 0 0 0;
  border-top: 1px solid #e0e0e0;
  font-size: 1.2rem;
  color: #626262;
}
.ymt-cars .content .item .right .right-one {
  height: 5rem;
  line-height: 5rem;
}
.ymt-cars .content .item .right .right-one .right-item :first-child {
  /*float:left;*/
  display: inline-block;
}
.ymt-cars .content .item .right .right-one .right-item :last-child {
