@keyframes show {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
.ymt-message {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  height: 3.85rem;
  max-width: 80%;
  margin: auto;
  text-align: center;
  z-index: 5000;
  /*animation:show 300ms 1; */
}
.ymt-message > .content {
  width: 11.5rem;
  height: 11.5rem;
  margin: 0 auto;
  border-radius: 0.5rem;
  background-color: #000;
  opacity: .8;
}
.ymt-message > .content .div-icon {
  width: 3.2rem;
  height: 5.9rem;
  margin: 0 auto;
  padding: 2.7rem 0 0 0;
  text-align: center;
  color: #fff;
}
.ymt-message > .content .div-icon i {
  font-size: 3.2rem;
}
.ymt-message > .content .div-space {
  height: 3rem;
}
.ymt-message > .content .div-img {
  width: 4rem;
  height: 2.4rem;
  margin: 0 auto;
  padding: 3rem 0 0 0;
  text-align: center;
  background-position: center;
  background-size: cover;
  background-image: url('../../assets/images/cars/loading@2x.gif');
}
.ymt-message > .content span {
  display: inline-block;
  margin: 1rem 0 2.7rem 0;
  color: #fff;
  font-size: 1.5rem;
}
.ymt-message > span {
  display: inline-block;
  padding: 1.2rem 1.6rem;
