  overflow: hidden;
}
.view-image .image img {
  width: 100%;
  /*height: 52.1rem; 		*/
  height: 100%;
}
.view-image .footer {
  position: fixed;
  bottom: 1.2rem;
  width: 100%;
  padding: 0 1.2rem;
  /*border: 1px solid red;*/
}
.no-network {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  background-color: #f0f0f0;
  z-index: 8000;
}
.no-network .content {
  position: fixed;
  top: 4.4rem;
  bottom: 0;
  left: 0;
  right: 0;
}
.no-network .content .content-img {
  margin: 5.75rem auto 0 auto;
  text-align: center;
}
.no-network .content .content-img img {
  width: 16rem;
  height: 16rem;
}
.no-network .content .content-des {
  margin: 2rem auto 0 auto;
  text-align: center;
  font-size: 1.5rem;
  color: #626262;
}
.no-network .content .content-button {
  margin: 5rem auto 0 auto;
  width: 15rem;
  text-align: center;
}



